<template>
  <div class="app-container">
    <div class="app-content" :class="{ shifted: isSidebarActive }">
      <h1 class="title"></h1>
      <form @submit.prevent="updatePreferences" class="preferences-form">
        <div class="form-group">
          <label for="font-size">:</label>
          <select v-model="preferences.fontsize" id="font-size" class="form-control">
            <option value="small"></option>
            <option value="medium"></option>
            <option value="large"></option>
          </select>
        </div>
        <div class="form-group">
          <label for="notification-settings">:</label>
          <select v-model="preferences.notificationSettings" id="notification-settings" class="form-control">
            <option :value="1"></option>
            <option :value="0"></option>
          </select>
        </div>
        <div class="form-group">
          <label for="auto-login">:</label>
          <select v-model="preferences.autoLogin" id="auto-login" class="form-control">
            <option :value="1"></option>
            <option :value="0"></option>
          </select>
        </div>
        <div class="form-group">
          <label for="authentication">:</label>
          <select v-model="preferences.authentication" id="authentication" class="form-control">
            <option :value="1"></option>
            <option :value="0"></option>
          </select>
        </div>
        <div class="btn-container">
          <button type="submit" class="btn btn-save"></button>
          <button type="button" class="btn btn-add" @click="showAddModal"></button>
          <button type="button" class="btn btn-delete" @click="deletePreference"></button>
          <button type="button" class="btn btn-cancel" @click="resetPreferences"></button>
        </div>
      </form>
  
      <div v-if="successMessage" class="success-message"></div>
  
      <!--  -->
      <div v-if="showModal" class="modal">
        <div class="modal-content">
          <span class="close" @click="closeAddModal">&times;</span>
          <h2 class="title"></h2>
          <form @submit.prevent="addPreference" class="preferences-form">
            <div class="form-group">
              <label for="add-font-size">:</label>
              <select v-model="newPreference.fontsize" id="add-font-size" class="form-control">
                <option value="small"></option>
                <option value="medium"></option>
                <option value="large"></option>
              </select>
            </div>
            <div class="form-group">
              <label for="add-notifications">:</label>
              <select v-model="newPreference.notificationSettings" id="add-notifications" class="form-control">
                <option :value="1"></option>
                <option :value="0"></option>
              </select>
            </div>
            <div class="form-group">
              <label for="add-auto-login">:</label>
              <select v-model="newPreference.autoLogin" id="add-auto-login" class="form-control">
                <option :value="1"></option>
                <option :value="0"></option>
              </select>
            </div>
            <div class="form-group">
              <label for="add-authentication">:</label>
              <select v-model="newPreference.authentication" id="add-authentication" class="form-control">
                <option :value="1"></option>
                <option :value="0"></option>
              </select>
            </div>
            <div class="btn-container">
              <button type="submit" class="btn btn-save"></button>
              <button type="button" class="btn btn-cancel" @click="closeAddModal"></button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'PersonalPreference',
  data() {
    return {
      preferences: {
        user_id: null,
        fontsize: '',
        notificationSettings: 0,
        autoLogin: 0,
        authentication: 0
      },
      newPreference: {
        fontsize: 'medium',
        notificationSettings: 0,
        autoLogin: 0,
        authentication: 0
      },
      successMessage: false,
      showModal: false,
      isSidebarActive: false
    };
  },
  created() {
    this.loadPreferences();
  },
  methods: {
    loadPreferences() {
      axios.get('/api/user_preferences/')
        .then(response => {
            this.preferences = response.data;
        })
        .catch(error => {
            console.error('Error loading preferences:', error);
        });
    },
    updatePreferences() {
      axios.post('/api/user_preferences/', this.preferences)
        .then(response => {
            if (response.status === 200) {
                this.showSuccessMessage();
            } else {
                alert('');
            }
        })
        .catch(error => {
            console.error('Error updating preferences:', error);
        });
    },
    showAddModal() {
      this.showModal = true;
    },
    closeAddModal() {
      this.showModal = false;
    },
    addPreference() {
      axios.post('/api/user_preferences/', this.newPreference)
        .then(response => {
            if (response.status === 200) {
                this.closeAddModal();
                this.showSuccessMessage();
            } else {
                alert('');
            }
        })
        .catch(error => {
            console.error('Error adding preference:', error);
        });
    },
    deletePreference() {
      axios.post('/api/user_preferences/', { user_id: this.preferences.user_id })
        .then(response => {
            if (response.status === 200) {
                this.showSuccessMessage();
            } else {
                alert('');
            }
        })
        .catch(error => {
            console.error('Error deleting preference:', error);
        });
    },
    resetPreferences() {
      this.loadPreferences();
    },
    showSuccessMessage() {
      this.successMessage = true;
      setTimeout(() => {
        this.successMessage = false;
      }, 3000);
    }
  }
};
</script>

<style scoped src="@/assets/css/backend/PersonalPreference.css"></style>

