<template>
  <div>
    <div class="profile-container">
      <div class="profile-card">
        <div class="profile-image">
          <!--  -->
          <img src="https://via.placeholder.com/150" alt="Profile Image" />
        </div>
        <div class="profile-info">
          <h2>{{ userData.username || 'N/A' }}</h2>
          <p>{{ userData.position_id || 'N/A' }}</p>
          <p>{{ userData.department_id || 'N/A' }}</p>
          <p>{{ userData.email || 'N/A' }}</p>
          <p>{{ userData.phone || 'N/A' }}</p>
          <button class="edit-button" @click="editProfile"></button>
        </div>
      </div>

      <div v-if="isEditing" class="edit-modal">
        <div class="edit-form">
          <h3></h3>
          <p><input type="text" v-model="editData.username" /></p>
          <p><input type="text" v-model="editData.department_id" disabled /></p>
          <p><input type="text" v-model="editData.position_id" disabled /></p>
          <p><input type="text" v-model="editData.phone" /></p>
          <p><input type="email" v-model="editData.email" /></p>
          <div class="button-container">
            <button @click="saveProfile" class="save"></button>
            <button @click="cancelEdit" class="cancel"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      userData: {
        username: '',
        email: '',
        phone: '',
        department_id: '',
        position_id: '',
      },
      editData: {},
      isEditing: false
    };
  },
  methods: {
    async fetchUserProfile() {
      try {
        const response = await axios.get('/api/frontend/profile/');
        this.userData = response.data;
      } catch (error) {
        console.error('Error fetching user profile:', error);
      }
    },
    editProfile() {
      this.editData = { ...this.userData };
      this.isEditing = true;
    },
    cancelEdit() {
      this.isEditing = false;
    },
    async saveProfile() {
      try {
        await axios.put('/api/frontend/profile/', this.editData);
        this.userData = { ...this.editData };
        this.isEditing = false;
        alert('');
      } catch (error) {
        console.error('Error saving profile:', error);
        alert('');
      }
    }
  },
  mounted() {
    this.fetchUserProfile(); // 
  }
};
</script>

<style scoped src="@/assets/css/frontend/ProfilePage.css"></style>
